import React, { Component } from 'react'
import './TodoItem.css'
class TodoItem extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isShow:false,
        };
        this.changeInfo=this.changeInfo.bind(this);
        this.setSomething=this.setSomething.bind(this);
        this.toogleShow=this.toogleShow.bind(this);
        this.removeItem=this.removeItem.bind(this);
    }
    toogleShow(){
        this.setState({isShow:!this.state.isShow});
    }
    changeInfo(){
        if(!this.state.isShow){
            this.setState({isShow:true});
        }
    }
    setSomething(e){
        this.props.onInfoChange(e.target.value,this.props.index);
    }
    componentDidUpdate(prevProps,prevState){
        if(this.state.isShow)this.refs.doSomething.focus();
    }
    removeItem(e,index){
        e.preventDefault();
        this.props.removeItem(this.props.index);
    }
    render() {
        return (
          <li>
            <h3 style={{display:"inline-block",marginRight:"15px"}}>{this.props.title}</h3>
            <a href="" onClick={(event) =>this.removeItem(event)}></a>
            <p onClick={this.changeInfo}>
                <span className={!this.state.isShow?'':'hide-input'}>{this.props.description}</span>
                <textarea  
                       value={this.props.description} 
                       onChange={this.setSomething} 
                       ref="doSomething" 
                       className={this.state.isShow?'show-input':'hide-input'}
                       onBlur={this.toogleShow}/>
            </p>
          </li>  
        );
    }
}

export default TodoItem;


