import React, { Component } from 'react';
import Grid from './Grid'
import './GridGame.css'

class GridGame extends Component {
    constructor(props) {
        super(props);
        this.state = {
            notes:new Array(9).fill(null),
            history:[{index:-1,state:null}],
            isOther:true,
            isEnd:null,
            current:0
        };
        this.someChecke=this.someChecke.bind(this);
    }
    someChecke(index){
        if(!this.state.isEnd){
            let _notes=this.state.notes.slice(),
                historyStarte=[],
                _history=[];
            _notes[index]=this.state.isOther?_notes[index]="": _notes[index]="O";
            historyStarte=[{index,state:_notes[index]}];
            _history=this.state.history;
            _history.splice(this.state.current+1);
            this.setState({});
            this.setState({notes:_notes,
                           isOther:!this.state.isOther,
                           history:_history.concat(historyStarte),
                           current:this.state.history.length});
        }
    }
    componentWillUpdate(nextProps,nextState){
        if(!nextState.isEnd){
            let _isEnd=havePlayerWin(nextState.notes);
            if(_isEnd)this.setState({isEnd:_isEnd});
            if(nextState.current === 9)this.setState({isEnd:""});
        }
    }
    timeBack(event,index){
        let _history=this.state.history,
            _notes=this.state.notes.slice(),
            _isOther=this.state.isOther,
            _current=this.state.current;
        if(index<_current){
            for(let i=_current;i>index;i--){
                _notes[_history[i].index]=null;
            }
        }else{
            for(let i=_current+1;i<=index;i++){
                _notes[_history[i].index]=_history[i].state;
            }
        }
        if(Math.abs(index-_current)%2 === 1)this.setState({isOther:!_isOther})
        this.setState({current:index,notes:_notes,isEnd:null})
        event.preventDefault();
    }
    render() {
        let showEnd;
        if(!this.state.isEnd){
            showEnd=<h3>{this.state.isOther?"":"O"}</h3>
        }else{
            showEnd=<h3>{this.state.isEnd}</h3>
        }
        return (
            <div className="container">
                <div className="grid-container">
                    <h1 className="grid-title"></h1>
                    {
                        this.state.notes.map((note,index) =>{
                            return <Grid key={index} 
                                        gridState={note} 
                                        index={index}
                                        someChecke={this.someChecke}/>
                        })
                    }
                </div>
                <div className="grid-info">
                    {showEnd}
                    <ol>
                        {this.state.history.map((h,index) => {
                            return <li key={index}>
                               <a href="" onClick={(event) => this.timeBack(event,index)}>
                                    {index>0?''+index:''}
                               </a> 
                            </li>
                        })}
                    </ol>
                </div>
            </div>
        );
    }
}
function havePlayerWin(notes){
    const winList=[
        [0, 1, 2],
        [3, 4, 5],
        [6, 7, 8],
        [0, 3, 6],
        [1, 4, 7],
        [2, 5, 8],
        [0, 4, 8],
        [2, 4, 6],
    ];
    for(let i=0;i<winList.length;i++){
        let [a,b,c]=winList[i];
        if(notes[a] && notes[a] === notes[b] && notes[a] === notes[c]){
            return notes[a];
        }
    }
    return null;

}
export default GridGame;


