import React, { Component } from 'react'
import { Switch , Route} from 'react-router-dom'
/* import logo from './logo.svg'; */
import TodoList from './components/TodoList'
import GridGame from './components/GridGame'
import Myheader from './components/Myheader'
import MyIndex from './components/MyIndex'
import FavIcon from './components/FavIcon'

import { createStore } from 'redux'
import { Provider, connect } from 'react-redux'
import todoReducer from './reducer'

import { Layout, notification} from 'antd'
import './App.css';
const { Header, Footer, Content } = Layout;



// Store
const store = createStore(todoReducer)

// Map Redux state to component props
function mapStateToProps(state) {
	return {
		Todos: state
	}
}

// Map Redux actions to component props
function mapDispatchToProps(dispatch) {
	return {
		onInfoChange: (value,index) => {
			dispatch({ type: 'CHANGE_TODO_CONTENT' ,index: index,value :value})
		},
		removeItem: (index) => {
			dispatch({ type: 'DELETE_ITEM' ,index: index})
		},
		addItem: (title,content) => {
			dispatch({ type: 'ADD_ITEM' ,title: title,content:content})
		}
	}
}
class App extends Component {
	componentWillMount(){
		notification.info({
			message : 'Hello',
			description : '~',
			duration : 2
		});
	}
	render() {
		return (
		<Provider store={store}>
			<div>
				<Layout>
					<Header style={{background:"#fff",padding:"0",margin:"0"}}>
						<Myheader />
					</Header>
					<Content style={{padding:"16px 12px",minHeight:document.body.clientHeight-136}}>
						<Switch>
							<Route path="/gridgame" exact component={GridGame} />
							<Route path="/todo" exact component={Todos} />
							<Route path="/"  component={MyIndex} />
						</Switch>
						<FavIcon />
					</Content>
					<Footer style={{background:"#ddd"}}>
						<h3 style={{margin:"0 auto",textAlign:"center"}}>Footer</h3>
					</Footer>
				</Layout>
				{/* <TodoList /> */}
				{/* <GridGame /> */}
			</div>
		</Provider>
		);
	}
}

// Connected Component
const Todos = connect(
	mapStateToProps,
	mapDispatchToProps
)(TodoList)
export default App;



