import React, { Component } from 'react'
import {Icon, notification, Popconfirm, Modal, Input} from 'antd'

class FavIcon extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            isShow:false,
            isAssess:false,
            visible: false,
            confirmLoading: false,
        };
        //this.sendAccess=this.sendAccess.bind(this);
    }
    favClick = (e) => {
        this.setState({isShow:!this.state.isShow})
    }
    sendAccess = (e) =>{
        let access=e.target.getAttribute('access');
        if(access){
            switch(access){
                case 'normal':
                    this.setState({visible:true});
                    break;
                case 'good':
                    notification.open({
                        message: '',
                        description: '',
                        icon: <Icon type="smile-circle" style={{ color: '#108ee9' }} />,
                    });
                    this.setState({isAssess:true});
                    break;
                default:
            }
            //this.setState({isAssess:true});
        }
    }
    handleCancel= () =>{
        this.setState({visible:false});
    }
    handleOk = () =>{
        this.setState({confirmLoading:true});
        setTimeout(()=>{
            this.setState({isAssess:true});
            this.setState({visible:false});
        },1500)
    }
    sendOk = () =>{
        this.setState({visible:true});
    }
    render() {
        return (
            <div className="fav-container" style={{display:this.state.isAssess?"none":""}}>
                <div className={this.state.isShow?"fav-dialog fav-toggle":"fav-dialog"} >
                    <h3>?</h3>
                    <p></p>
                    <div className="rates" onClick={this.sendAccess}>
                        <div className={this.state.isShow?"rate fav-i-in1 i-opacity":"rate"}>
                            <Popconfirm title="" onConfirm={this.sendOk} okText="" cancelText="">
                                <Icon type="frown-o" access="bad" />
                            </Popconfirm>                        
                        </div>
                        <div className={this.state.isShow?"rate fav-i-in2 i-opacity":"rate"}>
                            <Icon type="meh-o" access="normal"/>
                        </div>
                        <div className={this.state.isShow?"rate fav-i-in3 i-opacity":"rate"}>
                            <Icon type="smile-o" access="good"/>
                        </div>
                    </div>
                    <p className="fav-star">
                        <a href="https://github.com/zyzcss/zyzcss.github.io" target="_blank" rel="noopener noreferrer">
                            <Icon type="github"/>star
                        </a>
                    </p>
                </div>
                <div className={this.state.isShow?"fav-icon":"fav-icon  fav-icon-toggle"} onClick={this.favClick}></div>
                <Modal title=""
                    visible={this.state.visible}
                    onOk={this.handleOk}
                    confirmLoading={this.state.confirmLoading}
                    onCancel={this.handleCancel}
                >
                    <p></p>
                    <Input.TextArea  />
                </Modal>
            </div>
            
        );
    }
}

export default FavIcon;


