import React, { Component } from 'react';
import TodoItem from './TodoItem.js';
import {Input, Button, message} from 'antd'
class TodoList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            newTitle:"",
            newContent:"",
        };
        this.changeTitle=this.changeTitle.bind(this);
    }
    changeTitle(e,info){
        switch(info){
            case "title":
                this.setState({newTitle:e.target.value});
                break;
            case "content":
                this.setState({newContent:e.target.value});
                break;
            default :
        }
    }
    addItem= () => {
        let title=this.state.newTitle,
            content=this.state.newContent;
        if(title!=="" && content!==""){
            this.props.addItem(title,content);
            this.setState({newTitle:"",newContent:""})
        }else{
            message.error("",1);
        }
       
    }
    render() {
        const {onInfoChange,removeItem,Todos}=this.props;
        return (
            <ul>
                <div className="Todo-input">
                    <Input placeholder="" 
                           value={this.state.newTitle}
                           onChange={(event) => this.changeTitle(event,"title")}/>
                    <Input.TextArea placeholder="" 
                                    style={{textIndent:"2px"}}
                                    value={this.state.newContent}
                                    onChange={(event) => this.changeTitle(event,"content")}/>
                    <Button onClick={this.addItem}></Button>
                </div>
                {Todos.items.map((item,index) => {
                    return <TodoItem key={index} 
                                     index={index}
                                     title={item.title} 
                                     description={item.description}
                                     onInfoChange={onInfoChange}
                                     removeItem={removeItem}/>
                })}
            </ul>
        );
    }
}

export default TodoList;


