const defaultState = {
    items:[{title:"",description:""},
    {title:"",description:""},
    {title:"",description:""}]
};
const todoReducer = (state = defaultState, action) => {
  switch (action.type) {
    case 'CHANGE_TODO_CONTENT':
        state["items"][action.index]["description"]=action.value;
        return Object.assign({},state);
    case 'DELETE_ITEM':
        state["items"].splice(action.index,1);
        return Object.assign({},state);
    case 'ADD_ITEM':
        state["items"].push({title:action.title,description:action.content})
        return Object.assign({},state);
    default: 
        return state;
  }
};
export default todoReducer;


