import { Carousel  } from 'antd';
import React, { Component } from 'react';
import slider1 from '../assets/1.jpg'
import slider2 from '../assets/2.jpg'
import slider3 from '../assets/3.jpg'
import slider4 from '../assets/4.jpg'
class MyIndex extends Component {
    constructor(props) {
        super(props);
        this.state = {  };
    }
    render() {
        return (
            <div className="silider-container">
                <Carousel autoplay  >
                    <img src={slider1} alt="" className="slider-img" />
                    <img src={slider2} alt="" className="slider-img"/>
                    <img src={slider3} alt="" className="slider-img"/>
                    <img src={slider4} alt="" className="slider-img"/>
                </Carousel>
            </div>
        );
    }
}

export default MyIndex;


