import React, { Component } from 'react'
import { Menu,Icon  } from 'antd'
import { Link } from 'react-router-dom'

class Myheader extends Component {
    constructor(props) {
        super(props);
        this.state = {
            current: 'index',
        }
    }
    handleClick = (e) => {
        this.setState({
            current: e.key,
        });
    }
    render() {
        return (
            <Menu
                onClick={this.handleClick}
                selectedKeys={[this.state.current]}
                mode="horizontal"
                style={{height:"64px",lineHeight:"64px",padding:"0 40px"}}
                theme="dark"
            >
                <Menu.Item key="index">
                    <Link to="/"><Icon type="home" /></Link>
                </Menu.Item>
                <Menu.SubMenu key="appstore" title={<span><Icon type="appstore" /><span></span></span>}>
                    <Menu.Item key="1">
                        <Link to="todo">Todo</Link>
                    </Menu.Item>
                    <Menu.Item key="2">
                        <Link to="gridgame"></Link> 
                    </Menu.Item>
                </Menu.SubMenu>
                <Menu.Item key="setting">
                    <Icon type="setting" />
                </Menu.Item>
                <Menu.Item key="alipay">
                    <a href="https://ant.design" target="_blank" rel="noopener noreferrer"></a>
                </Menu.Item>
            </Menu>
        );
    }
}

export default Myheader;


