import React, { Component } from 'react'


class Grid extends Component {
    constructor(props) {
        super(props);
        this.state = {  };
        this.someChecke=this.someChecke.bind(this);
    }
    someChecke(){
        if(!this.props.gridState)this.props.someChecke(this.props.index);
    }
    render() {
        let gridState=this.props.gridState?<p>{this.props.gridState}</p>:<p> </p>;
        return (
            <button className="grid-box" onClick={this.someChecke}>
                {gridState}
            </button>
        );
    }
}
export default Grid;


